<template>
	<section>
		<div class="zx-banner">
      <img src="../../../static/images/zx_banner3.jpg" alt="">
      <div class="zx-myho">
        <span><img :src="userInfor.imgurl || '../../static/images/enter_bg1.jpg'" alt=""></span>
        <em>{{userInfor.nickName|| '昵称'}}</em>
        <p>个人签名：{{userInfor.personalNote}}</p>
      </div>
    </div>
    
    <ul class="zx-h-menu clearfix persontab">
       <li><a href="javascript:;" class="cur">订阅</a></li>
       <li><a href="javascript:;">评论</a></li>
       <li><a href="javascript:;">收藏</a></li>
    </ul>
    
    <div class="inner clearfix divtab" style="display: block;min-height: 400px;">
      <ul class="zx-h-dycon clearfix">
        <li v-for="item in takeList">
          <div class="zx-h-dycon-t">
            <img :src="item.columnCover" alt="" @click="getZL(item.id)">
            <p style="line-height: 24px; height: 24px;">{{item.columnTitle||''}}</p>
            <span :title="item.columnBrief" style="height: 48px;">{{item.columnBrief|truncate(50)}}</span>
          </div>
          <h1><em>最新更新</em></h1>
          <ul class="zx-h-dycon-lis clearfix"  style="height: 70px;">
            <li v-for="(data,index) in item.infoList" @click="getArticle(data.uuid)" v-if="index<3" style="overflow: hidden;"><a href="javascript:;" :title="data.title">{{data.title|truncate(16)}}</a></li>
          </ul>
        </li>
      </ul>
      <div class="c-page zx-page1">
            <el-pagination
       			:page-size="pageSize1"
		      	@current-change="pageFn1"
			  	layout="prev, pager, next"
			  	:total=totalone>
			</el-pagination>
	   </div>
    </div>
	
	<div class="divtab" style="min-height: 400px;">
	<!--<div class="inner clearfix ">
      <div class="zx-h-plt">
        <h1><i class="zx-icon zx-ico-pli"></i>面试题解析：如何估算煎饼摊大妈一个月的收入？<span>2018-03-08 13:38:11</span></h1>
        <textarea class="zx-txt triangle-up ">我有个大胆的想法......</textarea>
        <button type="button" class="zx-btn-pl">评论</button>
      </div>
   </div>-->
    <div class="inner clearfix ">
      <!--<ul class="zx-h-plcon clearfix">
          <li class="zx-myhr-zz-r zx-h-sc zx-h-pln" v-for="item in commonList">
            <div>
              <img :src="item.userImg" alt="">{{item.createUser}}  
              <span>{{item.createDate|timestampFormat('YYYY-MM-DD HH:mm')}}<a href="javascript:;">回复</a></span>
            </div>
            <p>{{item.commentContent}}</p>
          </li>
      </ul>-->
    <ul class="zx-myhr-lis">
      <li v-for="item in commentList">
        <a href="javascript:;">{{item.commentContent}}</a>
        <em @click="getArticle(item.uuid)" style="cursor: pointer;">评论文章：{{item.title}}</em>
        <span class="zx-myh-pld">
          <!--<i class="zx-icon zx-ico-del"></i><br/>-->
          <i ></i><br/>
          <i class="zx-icon zx-ico-tim"></i>{{item.updateDate|timestampFormat('YYYY年MM月DD日  HH:mm:ss')}}
        </span>
      </li>
    </ul>
      <div class="c-page zx-page">
            <el-pagination
       			:page-size="pageSize2"
		      	@current-change="pageFn2"
			  	layout="prev, pager, next"
			  	:total=totaltwo>
			</el-pagination>
		</div>
    </div>
	</div>

		 <div class="inner divtab"style="min-height: 400px;">
      <ul class="zx-myhr-zlwz clearfix zx-myhr-hov">
          <li class="clearfix" v-for="item in collectlist">
            <div class="zx-myhr-zz-r zx-h-sc">
              <h2 @click="getArticle(item.uuid)"><a href="javascript:;" :title="item.title">{{item.title | truncate(45)}}</a></h2>
              <!--<p>{{item.tex|truncate(80)}}</p>-->
              <div style="margin-top: 30px;">
                <img :src="item.columnCover" alt=""  @click="getZL(item.columnId)">{{item.columnTitle}}<em>{{item.createDate|timestampFormat('YYYY-MM-DD')}}</em>   
                <span>
                  <i class="zx-icon zx-ico-yl"></i>{{item.browseCount}}
                  <i class="zx-icon zx-ico-sc1"></i>{{item.collectionCount}}
                  <i class="zx-icon zx-ico-dz"></i>{{item.fabulousCount}}
                </span>
              </div>
            </div>
          </li>
        </ul>
    </div>

	</section>
</template>

<script>
	import {information,take,commentl,collect} from '../../api/info';
	export default{
		data(){
			return{
				userInfor:{},
				memberId:'',
				pageNum1:1,
				pageSize1:6,
				takeList:[],
				totalone:0,
				pageSize2:4,
				totaltwo:0,
				commentList:[],
				collectlist:[],
				totalthree:0,
				pageSize3:3,
			}
		},
		methods:{
			getZL(name){
				this.$router.push({path:'/zlDetails',query:{id:name}})
				//window.open()
			},
			getArticle(uuid){
				this.$router.push({path:'/ZLactive',query:{uuid:uuid}})
			},
			informationFn(){
				var param={
					memberId:this.memberId
				}
				information(param).then(res=>{
					if(res.datas){
						this.userInfor=res.datas;
					}
					
				})
			},
			takeFn(){//我的订阅
				var param = {
					memberId:this.memberId,
					pageNum:this.pageNum1,
					pageSize:this.pageSize1
				}
				take(param).then(res=>{
					this.totalone=res.total;
					this.takeList = res.datas;
					
				})
			},
			pageFn1(sty){
				this.pageNum1=sty
				var param = {
					memberId:this.memberId,
					pageNum:sty,
					pageSize:this.pageSize1
				}
				take(param).then(res=>{
					this.totalone=res.total;
					this.takeList = res.datas;
				})
			},
			getArticle(uuid){
				this.$router.push({path:'/ZLactive',query:{uuid:uuid}})
			},
//			commonListFn(){//我的评论
//				var param={
//					infoUuid:this.infoUuid,
//					pageNum:this.pageNum,
//					pageSize:4,
//					memberId:this.memberId
//				}
//				commonList(param).then(res=>{
//					this.commonList=res.datas;
//					this.totaltwo= res.total;
//				})
//			},
			commentFn(){//我的评论
	 			var param={
					pageNum:1,
					pageSize:4,
					memberId:this.memberId
	 			}
	 			commentl(param).then(res=>{
	 				this.totaltwo= res.total;
	 				
	 				this.commentList = res.datas
	 			})
	 		},
			pageFn2(str){
				var param={
					pageNum:str,
					pageSize:4,
					memberId:this.memberId
	 			}
	 			commentl(param).then(res=>{
	 				this.totaltwo= res.total;
	 				
	 				this.commentList = res.datas
	 			})
			},
			myCollect(){//我的收藏
				 var param={
				 	//pageNum:1,
				 	//pageSize:12
				 	memberId:this.memberId
				 }
				collect(param).then(res=>{
					console.log(res.datas)
					this.collectlist = res.datas;
					this.totalthree=res.total;
				})
			},
			tabFn(){
				$('.persontab li').on('click',function(){
					$(this).children('a').addClass('cur').parent().siblings('li').children('a').removeClass('cur');
					$('.divtab').hide();
					$('.divtab').eq($(this).index()).show();	
				})
			}
		},
		mounted(){
			this.tabFn();
			this.informationFn();
			this.takeFn();
			this.commentFn();
			this.myCollect()
		},
		created(){
			this.memberId=this.$route.query.id
			console.log(this.memberId)
		}
	}
</script>

<style>
	.divtab{
		display: none;
	}
</style>


